import React, { useState } from "react"
import { useStaticQuery, graphql } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import MenuItem from "../../components/menuitem.js"

import { Icon, Row, Col } from "react-materialize"

const Skewers_Menu_Page = () => {
  const images = useStaticQuery(graphql`
    query {
      ginger_grilled_pork: file(
        relativePath: { eq: "a la karte/ginger_grilled_pork.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  const [numTotalItems, setItems] = useState(0)

  const addItem = () => {
    setItems(numTotalItems + 1)
  }

  const removeItem = () => {
    setItems(numTotalItems - 1)
  }

  return (
    <Layout>
      <SEO title="Skewers | Online Ordering" />
      <div style={{ marginTop: "4%", marginLeft: "10%", width: "80%" }}>
        <Row>
          <Col s={4}>
            <MenuItem
              key={"1"}
              title="Steamed Soybeans"
              price={5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.bacon_ceasar_salad.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"2"}
              title="Vegetable Salad"
              price={8}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.bacon_ceasar_salad.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"3"}
              title="Potato Salad"
              price={6}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.bacon_ceasar_salad.childImageSharp.fluid}
            />
          </Col>
        </Row>
      </div>
    </Layout>
  )
}

export default Skewers_Menu_Page
